@import url(https://fonts.googleapis.com/css?family=Bitter|Open+Sans);
.Intro {
  background-color: #444;
}

.Intro p {
  max-width: 500px;
  margin: auto;
  margin-bottom: 20px;
}

.Events {
  background-color: #333;
}

.Events > div {
  max-width: 500px;
  margin: auto;
  /* text-align: left; */
}

.Resources {
  background-color: #222;
}

.Resources > a {
  display: inline-block;
  width: 48px;
  height: 48px;
  margin: 0 5px;
  /* background-color: #56F89A; */
  background-color: #666;
}

.SimpleIcon {
  width: 32px;
  padding: 8px;
}

.Members {
  flex-wrap: wrap;
  background-color: #111;
}

.Members > div {
  margin: 40px auto;
  max-width: 800px;
}

.Member {
  display: inline-block;
  line-height: 0px;
  padding: 3px;
}

.Member:hover {
  text-decoration: none;
}

.Member canvas {
  filter: progid:DXImageTransform.Microsoft.BasicImage(grayscale=1);
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
}

.Member canvas:hover,
.Member canvas.selected {
  filter: none;
  -webkit-filter: none;
  filter: none;
}

.App {
  display: flex;
  flex-direction: column;
}

.App header {
  display: flex;
  color: #666;
}

.App header img {
  width: 60px;
  border-top-right-radius: 20%;
  border-bottom-right-radius: 20%;
}

.App header h1 {
  font-family: monospace;
  margin-left: 10px;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  /* background-color: #FF009E; */
  /* background-color: #FFE521; */
  /* background-color: #56F89A; */
  background-color: #222;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  line-height: 1.5em;
  color: #ccc;
}

h1 {
  font-size: 1.6em;
}

h1, h2 {
  font-family: 'Bitter', sans-serif;
}

h2 {
  margin-top: 0;
  color: #f0f0f0;
}

a {
  color: #FF009E;
  text-decoration: none;
  border-radius: 4px;
}

a:hover {
  color: #fff;
  background-color: #FF009E;
}

section {
  text-align: center;
  padding: 10% 5%;
}

@media (min-width: 640px) {
  section {
    padding: 3%;
  }
}

